import React from 'react';
import * as styles from './styles/HamburgerIcon.module.css';

interface HamburgerIconMenuProps {
  className: string;
  mobileMenuOpen: boolean;
  setMobileMenuOpen: (id: boolean) => void;
}

export const HamburgerIcon: React.FC<HamburgerIconMenuProps> = ({ className, mobileMenuOpen, setMobileMenuOpen }) => {
  function handleHamburgerClick() {
    setMobileMenuOpen(!mobileMenuOpen);
  }

  let cssStyles = `${styles.hamburger} ${className}`
  if (mobileMenuOpen) cssStyles += ` ${styles.cross}`

  return (
    <div className={cssStyles} onClick={() => handleHamburgerClick()}>
      <span className={`${styles.line} ${styles.one}`} />
      <span className={`${styles.line} ${styles.two}`} />
      <span className={`${styles.line} ${styles.three}`} />
      <span className={`${styles.line} ${styles.four}`} />
    </div>
  );
};
