import React from 'react';
import { Games } from './subcategories/Games';
import { About } from './subcategories/About';
import { TournamentsAndPromotions } from './subcategories/TournamentsAndPromos';
import {Support} from './subcategories/Support';
import * as styles from './styles/SubCategoryMenu.module.css';
import {
  TOURNAMENTS_AND_PROMOTIONS,
  GAMES,
  ABOUT,
  SUPPORT,
  CATEGORY_NONE
} from './CategoryNames';

interface SubCategoryMenuProps {
  className: string;
  selected: string;
  onLinkClicked: () => void;
}

export const SubCategoryMenu: React.FC<SubCategoryMenuProps> = ({ className, selected, onLinkClicked }) => {
  const subMenus: { [key: string]: JSX.Element } = {
    [TOURNAMENTS_AND_PROMOTIONS]: <TournamentsAndPromotions onLinkClicked={onLinkClicked} />,
    [GAMES]: <Games onLinkClicked={onLinkClicked} />,
    [ABOUT]: <About onLinkClicked={onLinkClicked} />,
    [SUPPORT]: <Support onLinkClicked={onLinkClicked} />,
  };
  
  let cssStyles = `${styles.transitionWrapper} ${className}`
  if (selected !== CATEGORY_NONE) cssStyles += ` ${styles.visibleStyle}`

  return (
    <div className={cssStyles}>
      {subMenus[selected]}
    </div>
  );
};
