import React, {useContext} from 'react'
import * as styles from './cookie-consent.module.css'
import cm from '../../helpers/class-merge'
import {CookieConsentContext} from '../../contexts/cookie-consent-context';

export default function CookieConsent() {
    const { popupVisible, setPopupVisible } = useContext(CookieConsentContext)

    function accept() {
        OneTrust.AllowAll()
        setPopupVisible(false)
    }

    function reject() {
        OneTrust.RejectAll()
        setPopupVisible(false)
    }

    return (
        <div className={cm(styles.container, popupVisible && styles.visible)}>
            <div className={styles.content}>
                <h3>We use Cookies</h3>
                <div className={styles.messageBlock}>
                <p className={styles.paragraph}>
                    By clicking "Accept All", you agree to the collection of your information for analytics and
                    experience personalization purposes; and to the sharing of your information with our social media
                    and advertising partners to assist with our marketing efforts.
                    <a className={styles.cookieLink} data-testid='cookie-notice-link' href={'/cookie-notice'} target='_blank'> Global Poker Cookie Notice</a>
                </p>
                </div>
                    <div className={styles.buttons}>
                    <button data-testid='cookie-reject-all-btn' className={styles.rejectButton} onClick={() => reject()}>
                        Reject All
                    </button>
                    <button data-testid='cookie-accept-all-btn' className={styles.acceptButton} onClick={() => accept()}>
                        Accept All
                    </button>
                </div>
            </div>
        </div>
    )
}
