import Link from 'src/components/Link';
import React from 'react';
import * as styles from './styles/SubCategoryList.module.css'

interface SubCategoryListProps {
    className: string,
    listHeading: {name: string, url: string},
    listItems: {name: string, url: string}[]
    onLinkClicked: () => void;
}

export const SubCategoryList: React.FC<SubCategoryListProps> = ({ className, listHeading, listItems, onLinkClicked }) => {
    return (
        <div className={`${styles.listContainer} ${className}`}>
          <div className={`${styles.listHeading} ${styles.text} `}>
            <Link className={styles.link} onClick={() => onLinkClicked()} to={listHeading.url}>{listHeading.name}</Link>
          </div>
            {listItems.map(item =>(
                <div key={item.name} className={`${styles.listItem} ${styles.text}`}>
                    <Link className={styles.link} onClick={() => onLinkClicked()} to={item.url}>{item.name}</Link>
                </div>
            ))}
        </div>
    )
}