import { graphql, useStaticQuery } from 'gatsby';
import Link from 'src/components/Link';
import { GatsbyImage } from 'gatsby-plugin-image';
import React from 'react';
import * as styles from '../styles/ImageCard.module.css'

interface ClockImageCardProps {
  className: string;
}

export const ClockImageCard: React.FC<ClockImageCardProps> = ({ className }) => {
    const images = useStaticQuery(
        graphql`
          query {
            clock: file(relativePath: { eq: "mega-menu/poker-clock.png" }) {
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
              }
            }
          }
        `
      );
    return (
        <div className={`${styles.imgContainer} ${className}`}>
            <Link className={`${styles.link}`} to='/online-poker-tournaments/poker-clock-timer'>
                <GatsbyImage className={`${styles.img}`} image={images.clock.childImageSharp.gatsbyImageData}  alt={'Clock icon'}/>
                <span className={`${styles.subtitle}`}>Get Our Clock App</span>
            </Link>
        </div>
    )
}