import React, { useRef, useEffect, useState } from 'react';
import { BlackButton } from '../buttons/BlackButton';
import { WhiteButton } from '../buttons/WhiteButton';
import { Category } from './Category';
import { HomeCategory } from './HomeCategory';
import { Logo } from './Logo';
import * as styles from './styles/MegaMenu.module.css';
import { SubCategoryMenu } from './SubCategoryMenu';
import {TOURNAMENTS_AND_PROMOTIONS, GAMES, CATEGORY_NONE, ABOUT, SUPPORT} from './CategoryNames';
import { HamburgerIcon } from './HamburgerIcon';
import { SignUpImageCard } from './image-cards/SignUpImageCard';

export const MegaMenu: React.FC<{ withLogoOnly?: boolean }> = ({ withLogoOnly = false }) => {
  const [selected, setSelected] = useState(CATEGORY_NONE);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const megaMenuNode = useRef<HTMLElement | null>(null);

  useEffect(() => {
    function handleHoverOutside(event: Event) {
      if (megaMenuNode !== null && megaMenuNode.current && !megaMenuNode.current.contains(event.target as Node)) {
        setSelected(CATEGORY_NONE);
      }
    }
    document.addEventListener('mouseover', handleHoverOutside, true);
  }, [megaMenuNode]);

  const selectedStyle = convertCategoryNameToCamelCase(selected);
  let containerStyle = `${styles.mainContainer} ${styles[selectedStyle]}`;
  mobileMenuOpen ? (containerStyle += ` ${styles.open}`) : (containerStyle += ` ${styles.closed}`);

  return withLogoOnly ? (
    <>
      <header className={containerStyle} role='banner' ref={megaMenuNode}>
        <Logo className={styles.logo} />
      </header>
    </>
  ) : (
    <>
      <header className={containerStyle} role='banner' ref={megaMenuNode}>
        <HamburgerIcon className={styles.hamburger} mobileMenuOpen={mobileMenuOpen} setMobileMenuOpen={setMobileMenuOpen} />
        <Logo className={styles.logo} />
        <HomeCategory className={styles.home} selected={selected} />
        <Category className={styles.tournaments} name={TOURNAMENTS_AND_PROMOTIONS} mobileMenuOpen={mobileMenuOpen} selected={selected} setSelected={setSelected} />
        <Category className={styles.games} name={GAMES} mobileMenuOpen={mobileMenuOpen} selected={selected} setSelected={setSelected} />
        <Category className={styles.about} name={ABOUT} mobileMenuOpen={mobileMenuOpen} selected={selected} setSelected={setSelected} />
        <Category className={styles.support} name={SUPPORT} mobileMenuOpen={mobileMenuOpen} selected={selected} setSelected={setSelected} />
        <div className={styles.buttons}>
          <WhiteButton url={`${process.env.GATSBY_GAMECLIENT_URL}/register`} buttonText='Register' className={`${styles.whiteBtn} ${styles.btn}`} />
          <BlackButton url={`${process.env.GATSBY_GAMECLIENT_URL}/login`} buttonText='Log In' className={`${styles.blackBtn} ${styles.btn}`} />
        </div>
        <SignUpImageCard className={styles.image} categoryMenu={true} />
        <SubCategoryMenu className={styles.subcategory} selected={selected} onLinkClicked={() => setMobileMenuOpen(false)} />
      </header>
    </>
  );
};

const convertCategoryNameToCamelCase = (str: string) => {
  const rmSpacesAmpersand = str.replace(/ /g, '').replace('&', '');
  const camelCase = rmSpacesAmpersand.charAt(0).toLowerCase() + rmSpacesAmpersand.slice(1);
  return camelCase;
};
