import React from 'react';
import { navigate } from 'src/components/Link';
import { CATEGORY_NONE } from './CategoryNames';
import * as styles from './styles/Category.module.css';

interface HomeCategoryProps {
    className: string;
    selected: string;
}

export const HomeCategory: React.FC<HomeCategoryProps> = ({className, selected}) => {
    let cssStyles = `${styles.category} ${className}`
    if (selected === CATEGORY_NONE) cssStyles += ` ${styles.selected} ${styles.homeSelected}`

    const clickHandler = () => {
        window.location.pathname === '/' ? window.location.reload() : navigate('/');
    }

    return (
        <div className={cssStyles}>
             <p onClick={() => clickHandler()} className={styles.categoryText}> {CATEGORY_NONE} </p>
        </div>
    );
}