import React from 'react';
import * as styles from './CTAButton.module.css';

interface BlackButtonProps {
  url: string;
  buttonText: any;
  className?: string;
  target?: string;
}

export const BlackButton: React.FC<BlackButtonProps> = ({ url, buttonText, className, target = '' }) => {

  return (
    <a target={target} href={`${url}${process.env.GATSBY_PLATFORM_PARAM}`} className={`${className} ${styles.blackBtn} ${styles.btn}`}>
      {buttonText}
    </a>
  );
};
