import React from 'react';
import { CATEGORY_NONE } from './CategoryNames';
import * as styles from './styles/Category.module.css';

interface CategoryProps {
    className: string;
    name: string;
    mobileMenuOpen: boolean;
    selected: string;
    setSelected: (str: string) => void;
}

export const Category: React.FC<CategoryProps> = ({className, name, mobileMenuOpen, selected, setSelected}) => {
    let cssStyles = `${styles.category} ${className}`
    if (selected === name) cssStyles += ` ${styles.selected}`

    const clickHandler = (name: string) => {
        name != selected ? setSelected(name) : setSelected(CATEGORY_NONE)
    }

    const withClickHandler = <p onClick={() => clickHandler(name)} className={styles.categoryText}> {name} </p>
    const withHoverHandler = <button onClick={() => clickHandler(name)} onMouseOver={() => setSelected(name)} className={styles.categoryText}> {name} </button>

    let categoryElement = mobileMenuOpen ? withClickHandler : withHoverHandler;

    return (
        <div className={cssStyles}>
             {categoryElement}
        </div>
    );
}