import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import React from 'react';
import * as styles from '../styles/ImageCard.module.css';

interface PlayPokerImageCardProps {
  className: string;
}

export const PlayPokerImageCard: React.FC<PlayPokerImageCardProps> = ({ className }) => {
  const images = useStaticQuery(
    graphql`
      query {
        playPoker: file(relativePath: { eq: "mega-menu/play-poker.png" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
      }
    `
  );
  return (
    <div className={`${styles.imgContainer} ${className}`}>
      <a className={`${styles.link}`} href={`${process.env.GATSBY_GAMECLIENT_URL}/login${process.env.GATSBY_PLATFORM_PARAM}`}>
        <GatsbyImage className={`${styles.img}`} image={images.playPoker.childImageSharp.gatsbyImageData}  alt={'Play Poker Image'}/>
        <span className={`${styles.subtitle}`}>Play Poker</span>
      </a>
    </div>
  );
};
