import { graphql, useStaticQuery } from 'gatsby';
import Link from 'src/components/Link';
import { GatsbyImage } from 'gatsby-plugin-image';
import React from 'react';
import * as styles from '../styles/ImageCard.module.css'

interface SignUpImageCardProps {
  className?: string;
  categoryMenu: boolean;
}

export const SignUpImageCard: React.FC<SignUpImageCardProps> = ({ className, categoryMenu }) => {
    const images = useStaticQuery(
        graphql`
          query {
            signUp: file(relativePath: { eq: "mega-menu/sign-up-promo.png" }) {
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
              }
            }
          }
        `
      );

      const categoryMenuImgStyles = categoryMenu ? styles.categoryMenu : null

    return (
        <div className={`${styles.imgContainer} ${categoryMenuImgStyles} ${className}`}>
            <Link className={`${styles.link}`} to='/sign-up-offer/'>
                <GatsbyImage className={`${styles.img} ${categoryMenuImgStyles}`} image={images.signUp.childImageSharp.gatsbyImageData}  alt={'Sign Up Image'}/>
                <span className={`${styles.subtitle}`}>Sign Up Offer</span>
            </Link>
        </div>
    )
}