import React from 'react';
import * as styles from './styles/Logo.module.css';
import { StaticImage } from 'gatsby-plugin-image';

export const Logo = (props: {className: string}) => {
  return (
    <div className={`${styles.imgContainer} ${props.className}`}>
      <a className={styles.logoLink} href='/'>
          <StaticImage
              placeholder='tracedSVG'
              layout='fixed'
              width={115}
              height={47}
              src='../../../static/images/gp-vertical-white-logo.png'
              alt='Global Poker Logo'
              aria-label='Global Poker Logo'
          />
      </a>
    </div>
  );
};
