import React from 'react';
import { ClockImageCard } from '../image-cards/ClockImageCard';
import { PlayPokerImageCard } from '../image-cards/PlayPokerImageCard';
import { SubCategoryList } from '../SubCategoryList';
import * as styles from '../styles/SubCategoryMenu.module.css';

interface TournamentsAndPromotionsProps {
  onLinkClicked: () => void;
}

interface MegaMenuLink {
    name: string;
    url: string;
    startDate: number | null;
    endDate: number | null
}

export const TournamentsAndPromotions: React.FC<TournamentsAndPromotionsProps> = ({ onLinkClicked }) => {
  return (
    <div className={styles.container}>
      <div className={styles.firstList}>
        <SubCategoryList
          className={styles.firstList}
          listHeading={{ name: 'The GP Championships', url: '/championships' }}
          listItems={activeChampionships}
          onLinkClicked={onLinkClicked}
        />
        <SubCategoryList
          className={styles.firstList}
          listHeading={{ name: 'Promotions', url: '/online-poker-promotions' }}
          listItems={activePromotions}
          onLinkClicked={onLinkClicked}
        />
      </div>
      <SubCategoryList
        className={styles.secondList}
        listHeading={{ name: 'Weekly Poker Tournaments', url: '/online-poker-tournaments' }}
        listItems={Poker_Tournaments}
        onLinkClicked={onLinkClicked}
      />
      <PlayPokerImageCard className={styles.firstImage} />
      <ClockImageCard className={styles.secondImage} />
    </div>
  );
};

const Championships: Array<MegaMenuLink> = [
  {
    name: 'Rattlesnake Open',
    url: '/online-poker-promotions/rattlesnake-open',
    startDate: null,
    endDate: null,
  },
  {
    name: 'Grizzly Games',
    url: '/online-poker-promotions/grizzly-games',
    startDate: null,
    endDate: null,
  },
  {
    name: 'The Goat',
    url: '/online-poker-promotions/the-goat',
    startDate: null,
    endDate: null,
  },
  {
    name: 'Eagle Cup',
    url: '/online-poker-promotions/eagle-cup',
    startDate: null,
    endDate: null,
  },
];

const Promotions: Array<MegaMenuLink> = [
  {
    name: 'GRIZZLY GAMES VII',
    url: '/online-poker-promotions/grizzly-games/',
    startDate: Date.UTC(2024, 0, 26, 5, 1),   // 26/01/24 0001H ET
    endDate: Date.UTC(2024, 1, 19, 4, 59)     // 18/02/24 2359ET
  },
];

const Poker_Tournaments: Array<{ name: string; url: string }> = [
  {
    name: 'Sunday Scrimmage',
    url: '/online-poker-promotions/sunday-scrimmage'
  },
  {
    name: 'Golden Scrimmage',
    url: '/online-poker-promotions/gold-coin-scrimmage'
  },
  {
    name: 'Gold Rush',
    url: '/online-poker-promotions/gold-rush'
  },
  {
    name: 'Bonanza',
    url: '/online-poker-promotions/bonanza'
  },
  {
    name: 'Daily Hundo',
    url: '/online-poker-promotions/daily-hundo'
  }
];

const currentDate = Date.now();
const activePromotions = Promotions.filter(promo => getActiveMegamenuLinks(promo));
const activeChampionships = Championships.filter(championship => getActiveMegamenuLinks(championship));

function getActiveMegamenuLinks (link: MegaMenuLink) {
  if (link.startDate == null && link.endDate == null)
    return true;
  else if (link.startDate != null && link.endDate == null)
    return link.startDate <= currentDate;
  else if (link.startDate != null && link.endDate != null)
    return link.startDate <= currentDate && link.endDate >= currentDate
}

